.search-container{
    display: flex;
    justify-content: center;
    margin: 10px;

}

.search-input{
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    border-width: 0;
    background: rgb(189, 118, 240);
    color:black;

    border-radius: 50px;
    box-shadow: o o 5px rebeccapurple;
}

.search-input::placeholder{
    color: black;
    
}


.search-input:focus{
    outline: none;
}
